import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, NavParams } from 'ionic-angular';
import { ExamDetailPage } from '../index';
import { ExamTypeMapper } from '../../models/exam-type.model';
import { ExamTypesProvider } from '../../providers/exam-types/exam-types';

/**
 * Generated class for the ExamsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-exams',
  templateUrl: 'exams.html',
})
export class ExamsPage {

  public searching: boolean = false;

  public currentItems: ExamTypeMapper[];

  constructor(public navCtrl: NavController, public navParams: NavParams,
              private examsPrv: ExamTypesProvider, private modalCtrl: ModalController) {
    this.getItems();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ExamTypesPage');
  }

  getItems() {
    this.searching = true;
    this.examsPrv.query().subscribe((data: ExamTypeMapper[]) => {
      this.searching = false;
      this.currentItems = data;
    });
  }

  addItem() {
    let specializationDetailModal = this.modalCtrl.create(ExamDetailPage);
    specializationDetailModal.onDidDismiss((data: any) => {
      if (data.reload) {
        this.getItems()
      }
    });
    specializationDetailModal.present();
  }

  openItem(item: ExamTypeMapper) {
    let specializationDetailModal = this.modalCtrl.create(ExamDetailPage, { item: item });
    specializationDetailModal.onDidDismiss((data: any) => {
      if (data.reload) {
        this.getItems();
      }
    });
    specializationDetailModal.present();
  }
}



